	module.exports = {"data":{"site":{"siteMetadata":{"title":"Apifarms","author":"ApiFarms"}},"markdownRemark":{"id":"/Users/Alex-TBS/Documents/code/bee-project/src/pages/index.md absPath of file >>> MarkdownRemark","html":"<h2>Welcome to ApiFarms!</h2>\n<p>Our mission is to combat the declining bee population and pollinator insects while taking an Holistic approach to urban and rural agricultural methods. </p>\n<p>We aim to do this by educating the public on maintaining rural and urban hives and wildflower mixes. Our hive service customers can expect  fully installed, continuously maintained hive for a full year and up to 70% of the honey harvest.  </p>\n<p>We pride ourselves on 4 core values authenticity, transparency, respect and adventure. At ApiFarms we see the need into educating the public on environmentally friendly practices with our authenticity and transparency values the public can be assured that they are receiving the best answers that we can provide. We do all our work while respecting the environment, the bees, the pollinator insects and the public. We understand that respect must be earned and we are confident that by listening attentively, understanding individual situations and being patient we will earn your respect. Finally but not least adventure, each of our relationships are great adventures and we always say that work can be fun. </p>\n<p>We look forward at serving our community and the environment for the years to come. Feel free to contact us with any related questions and we’ll be happy to get back to you in a brief delay.       </p>","frontmatter":{"title":"","date":null,"exclude_hr":"true","exclude":"true","image":null}}},"pathContext":{"slug":"/"}}

